  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.main {
  display: flex;
  flex-direction: row;
  height: 100vh;
  overflow: hidden;
}

.left-side {
  width: 20%;
  background-color: #f4f5f7;
  border-right: 1px solid #ddd;
  padding: 1rem;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.right-side {
  flex: 1;
  background-color: #fff;
  padding: 1rem;
  overflow-y: auto;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.nav-btn, .project-button, .project-header button {
  padding: 0.5rem 1rem;
  margin-right: 0.5rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.project-header button {
  width: 80px;
  font-size: 20px;
  font-weight: bolder;
}
.nav-btn:hover, .project-header button:hover {
  background-color: #0056b3;
}

.project-list {
  margin-top: 1rem;
  padding: 0;
  list-style: none;
}

.project-list li {
  margin-bottom: 0.5rem;
  padding: 0.5rem;
  background-color: black;
  border-radius: 4px;
}

.project-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.todo-item {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
  margin-bottom: 1rem;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.todo-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.todo-div {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.todo-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
}

.todo-project {
  font-weight: bolder;
  color: #555;
  font-size: 20px;
}

.todo-priority {
  padding: 0.2rem 0.5rem;
  font-size: 0.9rem;
  color: white;
  background-color: orange;
  border-radius: 4px;
  display: inline-block;
}

.todo-due-date {
  font-size: 1rem;
  color: #333;
}

.todo-description {
  font-size: 0.9rem;
  color: #666;
}

.todo-btn, .deletebtn {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 0.5rem;
  color: white;
}

.todo-delete-btn, .deletebtn {
  background-color: red;
}

.project-button {
  width: 150px;
  text-align: center;
}

.todo-delete-btn:hover, .deletebtn:hover {
  background-color: darkred;
}

.update-btn {
  background-color: #007bff;
}

.update-btn:hover {
  background-color: #0056b3;
}

.pending {
  padding: 0.3rem 0.7rem;
  font-size: 0.9rem;
  background-color: #ffeb3b;
  color: #333;
  border-radius: 4px;
  display: inline-block;
}

.complete-btn {
  background-color: green;
}

.complete-btn:hover {
  background-color: darkgreen;
}

.project-header {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.project-div {
  display: flex;
  gap: 50px;
  justify-content: space-evenly;
}

.title-project-pending-div, .due-date-priority-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

dialog form input,
dialog form select,
dialog form textarea {
  margin-bottom: 1rem;
  width: 100%;
  font: inherit;
  padding: 4px;
  border-radius: 5px;
  border: 1px solid #ccc;
  transition: all 0.3s ease;
}

dialog form input:focus,
dialog form select:focus,
dialog form textarea:focus {
  border-color: #007bff;
  background-color: #eaf4ff;
  outline: none;
}

dialog button {
  border: none;
  padding: 8px;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  background-color: blue;
}

dialog form button {
  float: right;
}


/* Media Queries for Mobile Responsiveness */
@media (max-width: 768px) {
  /* Adjust main container to stack items vertically */
  .main {
    flex-direction: column;
  }

  /* Make sidebar full width on mobile */
  .left-side {
    width: 100%;
    border-right: none;
    box-shadow: none;
  }

  /* Adjust the right side layout */
  .right-side {
    width: 100%;
    padding: 1rem;
  }

  /* Stacked project list */
  .project-list {
    flex-direction: column;
    gap: 10px;
  }

  .todo-item {
    padding: 1rem;
    font-size: 1rem; /* Adjust font size */
  }

  /* Stack todo items vertically */
  .todo-div {
    flex-direction: column;
  }

  .nav-btn {
    font-size: 0.9rem;
  }

  .project-button {
    width: 100%;
  }

  /* Adjust button sizes for mobile */
  .nav-btn, .project-button, .todo-btn, .deletebtn {
    padding: 0.5rem;
    font-size: 0.8rem;
  }

  /* Reduce padding in project header */
  .project-header {
    flex-direction: column;
    align-items: flex-start;
  }
}

/* For very small screens (phones in portrait mode) */
@media (max-width: 480px) {
  .todo-title {
    font-size: 1.2rem; /* Smaller title font */
  }

  .todo-project {
    font-size: 1rem; /* Adjust font size for project name */
  }

  .todo-priority {
    font-size: 0.8rem; /* Adjust priority font size */
  }

  .todo-description {
    font-size: 0.8rem; /* Adjust description font size */
  }
